    -webkit-transform: rotate3d(0, 0, 1, 45deg) scale3d(1, 1, 1);
    -o-transform: rotate3d(0, 0, 1, 45deg) scale3d(1, 1, 1);
}

.dlab-img-overlay12:after {
    background-color: rgba(0, 0, 0, 0);
