.theme-btn span {
    display: table-cell;
    vertical-align: middle;
    font-size: 16px;
    letter-spacing: -15px;
    opacity: 0;
    line-height: 50px;
    transition: all 0.5s;
    -webkit-transition: all 0.5s;
    -moz-transition: all 0.5s;
    text-transform: uppercase;
}

.theme-btn:hover span {
    opacity: 1;
    letter-spacing: 1px;
    padding-left: 10px;
}

